@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Titillium Web', sans-serif;
}
.home, .projects {
    display: flex;
    height: 60vh;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
}

/*About style*/
.about {
    font-family: monospace;
    min-height: 100vh;
    color: #232d36;
    background-color: #fde311;
    padding: 6vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3rem;
    /*height: 60vh;*/
}
.typical p{
    font-size: 2rem;
    font-family: monospace;

}

.story-description p{
    padding: 6vh;
    font-size: 1rem;
    font-family: monospace;
    margin: 0 5rem;
    color: #232d36;
    letter-spacing: 1px;
}


/*Home style*/

.profile-img {
    border-radius: 50%;
    height: 50vh;
    border: #90ac58 1px;
    margin-top: 50px;

 }
.link-icons {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 3px;
    cursor: pointer;
}


.navbar {
    background-color: #282c34;
    height:80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    background-color: #282c34;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}
.nav-menu.active{
    left: 0;
    transition: 350ms;
}
.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover{
    background-color: gold;
}
.nav-menu-items {
    width:100%;
    color: #f5f5f5;
}
.navbar-toggle{
    background-color: #282c34;
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
span {
    margin-left: 10px;
}
.background-img {

    background-size: cover;
    background: url(/static/media/creioane.8f407eaf.jpg) no-repeat;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    height: 50vh;
    width: 100%;

}
.welcome-message{
    font-family: monospace;
    color: crimson;
    font-weight:800;
    font-size:30px;
    text-align: center;
    display:flex;
    justify-content:center;
    align-items:center;
    position: relative;
    z-index: -1;
}
.img-links {
    display: flex;
    flex-direction: column;
    align-items: center;

}
.profile-img {
    border-radius: 50%;
    height: 25vh;
    border: #90ac58 1px;
    margin-top: 30px;
    position: relative;
    z-index: -1;


}
.link-icons {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 3px;
    cursor: pointer;
    height: 15vh;
    position: relative;
    z-index: -1;
}



